<template>
    <div id="app"></div>
    <TodoListVue :todos="todos" @delete-todo="deleteTodo" />
</template>

<script>
import TodoListVue from './components/TodoList.vue';

export default {
    name: 'App',
    data() {
        return {
            todos: ['Ремонт', 'Обед', 'Работа'],
        };
    },
    components: {
        TodoListVue,
    },
    methods: {
        deleteTodo(i) {
            this.todos.splice(i, 1);
        },
    },
};
</script>

