<template>
    <ul>
        <li v-for="(todo, i) in todos" @click="deleteTodo(i)" :key="i" class="pointer">
            {{ todo }}
        </li>
    </ul>
</template>

<script>
export default {
    props: ['todos'],
    methods: {
        deleteTodo(i) {
            this.$emit('delete-todo', i);
        },
    },
};
</script>

<style scoped>
.pointer {
    cursor: pointer;
}
</style>
